.text {
    &-align {
        &-left {
            text-align: right !important;
        }

        &-center {
            text-align: center !important;
        }

        &-right {
            text-align: right !important;
        }
    }

    &-ellipsis {
        display: block;
        white-space: nowrap;
        overflow: hidden;
        text-overflow: ellipsis;
        max-width: 72px;
    }

    &-link {
        color: $color-blue-60;
        text-decoration: underline;

        &:hover {
            color: $color-blue-60;
            text-decoration: none;
        }

        &:focus-visible {
            outline: 0;
            border-color: #fff;
            box-shadow: 0px 0px 0px 2px $color-blue-50;
        }
    }

    &-ws-nowrap {
        white-space: nowrap;
    }
}