.breadcrumb {
    background-color: $color-white;
    padding: 1.25rem 0rem;
    border-radius: 0.375rem;

    &-list {
        list-style: none;
        padding: 0;
        margin: 0;
        display: flex;
    }

    &-item {
        display: flex;
        align-items: center;
        color: $color-gray-60;
    }

    &-link {
        color: $color-blue-50;
        text-decoration: underline;

        &:hover {
            color: $color-blue-60;
        }

        &:active {
            color: $color-blue-60;
        }

        &:focus-visible {
            outline: 0;
            color: $color-blue-50;
            box-shadow: 0px 0px 0px 2px #FFF, 0px 0px 0px 3px #006DFA, 0px 0px 0px 5px #CCE4FF;
        }
    }

    &-separator {
        margin: 0 0.5rem;
        color: $color-gray-60;
    }

    &-current {
        font-weight: 400;
    }
}