.tooltip-container {
    position: relative;
    display: inline-block;
    white-space: normal;

    .tooltip-trigger {
        cursor: pointer;
    }

    .tooltip-content {
        visibility: hidden;
        width: 167px;
        background-color: rgba(54, 58, 65, 0.90);
        color: $color-white;
        font-size: 12px;
        text-align: center;
        border-radius: 4px;
        padding: 0.5rem 0.75rem;
        position: absolute;
        z-index: 1;
        opacity: 0;
        transition: opacity 0.3s ease-in-out;
    }

    // Posiciones del tooltip
    &.tooltip-top .tooltip-content {
        bottom: calc(100% + 10px);
        left: 50%;
        transform: translateX(-50%);
    }

    &.tooltip-bottom .tooltip-content {
        top: calc(100% + 10px);
        left: 50%;
        transform: translateX(-50%);
    }

    &.tooltip-left .tooltip-content {
        top: 50%;
        right: calc(100% + 10px);
        transform: translateY(-50%);
    }

    &.tooltip-right .tooltip-content {
        top: 50%;
        left: calc(100% + 10px);
        transform: translateY(-50%);
    }

    &.tooltip-bottom-left .tooltip-content {
        top: calc(100% + 2px);
        left: 0;
        transform: translateX(0);
    }

    &.tooltip-bottom-right .tooltip-content {
        top: calc(100% + 2px);
        right: 0;
        transform: translateX(0);
    }

    &:hover .tooltip-content {
        visibility: visible;
        opacity: 1;
    }
}